package dr.ulysses.database

import app.cash.sqldelight.Query
import app.cash.sqldelight.TransacterImpl
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Long
import kotlin.String

public class SongQueries(
  driver: SqlDriver,
) : TransacterImpl(driver) {
  public fun <T : Any> selectAll(mapper: (
    path: String,
    title: String,
    album: String?,
    artist: String,
    duration: Long?,
    state: String?,
  ) -> T): Query<T> = Query(1_433_118_721, arrayOf("song"), driver, "Song.sq", "selectAll",
      "SELECT song.path, song.title, song.album, song.artist, song.duration, song.state FROM song") {
      cursor ->
    mapper(
      cursor.getString(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2),
      cursor.getString(3)!!,
      cursor.getLong(4),
      cursor.getString(5)
    )
  }

  public fun selectAll(): Query<Song> = selectAll { path, title, album, artist, duration, state ->
    Song(
      path,
      title,
      album,
      artist,
      duration,
      state
    )
  }

  public fun selectAllArtists(): Query<String> = Query(823_797_451, arrayOf("song"), driver,
      "Song.sq", "selectAllArtists", "SELECT DISTINCT artist FROM song") { cursor ->
    cursor.getString(0)!!
  }

  public fun insert(
    title: String,
    album: String?,
    artist: String,
    path: String,
    duration: Long?,
    state: String?,
  ) {
    driver.execute(971_798_333,
        """INSERT INTO song(title, album, artist, path, duration, state) VALUES(?, ?, ?, ?, ?, ?)""",
        6) {
          bindString(0, title)
          bindString(1, album)
          bindString(2, artist)
          bindString(3, path)
          bindLong(4, duration)
          bindString(5, state)
        }
    notifyQueries(971_798_333) { emit ->
      emit("song")
    }
  }

  public fun update(
    title: String,
    album: String?,
    artist: String,
    duration: Long?,
    state: String?,
    path: String,
  ) {
    driver.execute(1_316_744_525,
        """UPDATE song SET title = ?, album = ?, artist = ?, duration = ?, state = ? WHERE path = ?""",
        6) {
          bindString(0, title)
          bindString(1, album)
          bindString(2, artist)
          bindLong(3, duration)
          bindString(4, state)
          bindString(5, path)
        }
    notifyQueries(1_316_744_525) { emit ->
      emit("song")
    }
  }

  public fun deleteByPath(path: String) {
    driver.execute(-696_096_725, """DELETE FROM song WHERE path = ?""", 1) {
          bindString(0, path)
        }
    notifyQueries(-696_096_725) { emit ->
      emit("song")
    }
  }
}
